@import "../../styles/main";

.ProductList {
  width: 100%;
  display: flex;
  gap: 16px;
  flex-wrap: wrap;

  @include on-mobile {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }

  &__item {
    display: flex;
    margin-bottom: 40px;

    @include on-tablet {
      margin-bottom: 16px;
    }
  }
}
